<template>
  <div class="F-AfterPin">
    <mt-header title="复销金额" fixed  class="F-top-return">
    <router-link to="/" slot="left">
        <mt-button icon="back" v-on:click="fanhui"></mt-button>
    </router-link>
    </mt-header>
    <p class="F-AfterPin-money">{{total_Award}}</p>
    <span class="F-AfterPin-yuan">复销金额（元）</span>
    <!-- 我的收益 -->
    <p class="F-AfterPin-withd">提现</p>
    <div class="F-AfterPin-earnings">
      <p>
        <span>工资收益</span>
        <b>{{wage}}</b>
      </p>
      <i></i>
      <p>
        <span>团队奖励</span>
        <b>{{teamAward}}</b>
      </p>
      <i></i>
      <p>
        <span>销售奖励</span>
        <b>{{sellAward}}</b>
      </p>
    </div>
    <div class="F-AfterPin-list">
      <ul
        v-infinite-scroll="loadMore"
        infinite-scroll-disabled="loading"
        infinite-scroll-distance="10">
        <li v-for="item in list" v-bind:key="item">{{ item }}</li>
      </ul>
      <p v-show="loading" class="F-AfterPin-loading"><mt-spinner color="#0a21f3" type="fading-circle"></mt-spinner>加载中。。。</p>
    </div>
  </div>
</template>
<script>
export default {
  name: 'home',
  data () {
    return {
      loading: false,
      list: []
    }
  },
  methods: {
    fanhui: function () {
      this.$router.go(-1)
    },
    loadMore: function () {
      console.log(1111111111)
      this.loading = true
      setTimeout(() => {
        for (let i = 1; i <= 10; i++) {
          this.list.push(i)
        }
        this.loading = false
      }, 2500)
    }
  }
}
</script>
<style lang='less' scoped>
@import './salesAmount.less';
</style>



